import React, { Component } from 'react'
import Busheader from '@/components/busHeader/index'
import './index.scss'
import signal from '@/assets/imgs/signal.gif'
import { selectLineCars } from '@/tool/api/real_bus.js'
import { _getDistance ,_TIMES} from '@/tool/Processing.js'
export default class Mero extends Component {
    constructor(props){
        super(props)
        const { meroAll } = props.location.state
        const { siteOrder } = meroAll
        this.state={
            parameter:meroAll,
            siteOrder:siteOrder,
            buslinebus:[]
        }
    }
    _GETLINEDATA = async ()=>{
        let that=this
        let timestamp = Date.parse(new Date());
        let { parameter,siteOrder } = that.state
        let { lat,long } = parameter
        let MYDATA={userId:timestamp,lineSiteCarsParams:[parameter]}
        const { data:LINEDATA,code } = await selectLineCars(MYDATA)
        if(code!==200) return;
        let lineBus=LINEDATA[0].list
        lineBus=lineBus.map(item=>{
            return {
              drawInTime:_TIMES(item.drawInTime),
              location_user:_getDistance(lat,long,item.lngLat.split(',')[1],item.lngLat.split(',')[0]),
              pullIn:item.pullIn,
              stat:siteOrder-item.nextNum,
              plateCode:item.plateCode
            }
        })
        lineBus=lineBus.filter((item)=>item.drawInTime!==-1)
        lineBus=lineBus.filter((item)=>item.pullIn!==1)
        lineBus=lineBus.sort((a,b)=>a.stat-b.stat) 
        that.setState({
            buslinebus:lineBus
        })
    }
    _SETDOM = (list)=>{
        let DOM=''
        if(list.pullIn===2) DOM=(<div className="left_pullIn2">
            <div className="pullIn2_top">
                <img src={signal} alt="" />
            </div>
            <div className="pullIn2_text">{list.drawInTime}</div>
            <div className="pullIn2_fl">{list.stat}站 / {list.location_user/1000}km</div>
        </div>)
        if(list.pullIn===3) DOM=(<div className="left_pullin3">已到站</div>)
        if(list.pullIn===4) DOM=(<div className="left_pullin3">即将到站</div>)
        return DOM
    }
    componentDidMount(){
        this._GETLINEDATA()
    }
    render() {
        const { topData }= this.props.location.state
        const { buslinebus } = this.state
        return (
            <div className="mero">
                <Busheader bus={ topData }></Busheader>
                <div className="mero_con">
                    {
                        buslinebus.map((item,index)=>{
                            return (<div className="con_box" key={index}>
                                    <div className="box_left">
                                        {this._SETDOM(item)}
                                    </div>
                                    <div className="box_right">
                                        <div className="right_text">{item.plateCode}</div>
                                    </div>
                                </div>)
                        })
                    }
                </div>
            </div>
        )
    }
}
